// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-sapphire:                    #102654;
$color-venetian-red:                #b80027;
$color-dark-cerulean:               #003d85;
$color-dark-cerulean:               #14376a;
$color-ghost-white:                 #f8f9fc;
$color-grey:                        #787878;
$color-crimson:                     #d10a42;
$color-egyptian-blue:               #103aae;
$color-torch-red:                   #ed0233;
$color-cobalt:                      #003eb7;
$color-whisper:                     #e8e8e8;
$color-torea-bay:                   #282b83;
$color-spindle:                     #b6bdcc;
$color-dark-gray:                   #afafaf;
$color-resolution-blue:             #1c3374;
$color-havelock-blue:               #5482dd;
$color-light-slate-grey:            #74819c;
$color-black-russian:               #030a1a;
$color-echo-blue:                   #aaacbf;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$header-gradient:                   conic-gradient(from 130.31deg at 16.03% 39.06%, $color-venetian-red -46.69deg, $color-dark-cerulean 32.24deg, $color-venetian-red 313.31deg, $color-dark-cerulean 392.24deg);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;
$lh-default:                        22px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-placeholder:                    "Placeholder", "Arial", sans-serif;
$ff-bebas-neue:                     "Bebas Neue", "Arial", sans-serif;
$ff-roboto-condensed:               "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1366:                           1366px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
